.questions {
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    margin-bottom: 8px;
  }
}

.questions__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 12px;
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin: 0 0 40px 0;
  }
}

.questions__list {
  display: flex;
  flex-direction: column;
  gap: 4px;

  @media (min-width: $desktop-width) {
    gap: 8px;
  }
}

.questions__item {
  background-color: $light-grey;
  border-radius: 16px;

  @media (min-width: $desktop-width) {
    border-radius: 24px;
  }
}

.questions__summary {
  padding: 16px 48px 16px 16px;
  font-family: "Manrope", Arial, sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  background-image: url("../images/svg/questions-plus.svg");
  background-position: right 16px top 16px;
  background-size: 24px 24px;
  background-repeat: no-repeat;
  list-style: none;
  cursor: pointer;

  @media (min-width: $desktop-width) {
    padding: 40px 80px 40px 40px;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
    background-position: right 40px top 40px;
    background-size: 32px 32px;
  }
}

.questions__subject[open] .questions__summary {
  background-image: url("../images/svg/questions-minus.svg");
}

.questions__answer {
  display: block;
  padding: 24px 16px;
  border-top: 1px solid $background-grey;
  font-family: "Manrope", Arial, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    padding: 40px;
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
  }
}

.questions__link {
  color: inherit;

  &:hover {
    color: $background-green;
  }
}
